.map-overlay {
  position: fixed;
  top: 0;
  left: 0;
  padding-top: 3vh;
  padding-bottom: 10px;
  height: 10vh;
  background: #2f4f4f;
  display: block;
  width: 100%;
  z-index: 999;
  display: flex;
  padding-left: 8%;
}

.map-overlay > i {
  color: grey;
  font-size: 20px;
  margin-top: 2.5vh;
}

.map-overlay-footer {
  position: fixed;
  bottom: 0;
  right: 0;
  padding-bottom: 10px;
  height: 14vh;
  background: white;
  width: 100%;
  z-index: 999;
  border-radius: 10px 10px 0px 0px;
}
.close-address-display {
  position: absolute;
  color: #2f4f4f;
  top: 8px;
  right: 12px;
}
.show-address {
  color: #2f4f4f;
  font-size: 14px;
  padding: 10px 40px 10px 10px;
  overflow: hidden;
}

.next-address-button {
  position: absolute;
  color: #2f4f4f;
  font-size: 16px;
  bottom: 8px;
  display: flex;
  right: 14px;
}
.next-address-button > i {
  margin: 4px;
  font-size: 18px;
}
